<template>
    <div>

        <b-row align-h="center">
            <b-col sm="12">
                <h2>Nous contacter</h2>
            </b-col>
            <b-col sm="12" md="8">
                <b-form @submit="onSubmit" class="justify-content-end">

                    <b-form-group
                            id="motif-group"
                            label="Motif du contact"
                            label-for="motif-input">
                        <b-form-select
                                id="motif-input"
                                v-model="form.motif"
                                :options="motifs"
                                required
                        ></b-form-select>
                    </b-form-group>

                    <b-form-group
                            id="email-group"
                            label="Adresse mail"
                            label-for="email-input">
                        <b-form-input
                                id="email-input"
                                v-model="form.email"
                                :state="emailValidation"
                                type="email"
                                required
                                placeholder="Entrez votre adresse mail"
                        ></b-form-input>
                        <b-form-invalid-feedback :state="emailValidation">
                            Adresse mail incorrecte
                        </b-form-invalid-feedback>
                        <b-form-valid-feedback :state="emailValidation">
                            Adresse mail correcte
                        </b-form-valid-feedback>
                    </b-form-group>

                    <b-form-group
                            id="message-group"
                            label="Message"
                            label-for="message-input">
                        <b-form-textarea
                                id="message-input"
                                v-model="form.message"
                                rows="5"
                                required
                        ></b-form-textarea>
                    </b-form-group>

                    <div class="text-right">
                        <b-button type="submit" variant="outline-primary">Envoyer</b-button>
                    </div>

                </b-form>
            </b-col>
        </b-row>

        <b-row>
            <b-col sm="12">
                <h2>Nos boutiques</h2>
            </b-col>
            <b-col cols="12" md="6" v-for="shop of shops">
                <shop :city="shop.city" :address="shop.address" :phone="shop.phone"
                      :openingHour="shop.openning_hours"></shop>
            </b-col>
        </b-row>

    </div>
</template>

<script>
    import Shop from '../components/Shop'

    export default {
        components: {
            Shop
        },
        data() {
            return {
                shops: [],
                form: {
                    motif: null,
                    email: '',
                    message: ''
                },
                motifs: [{
                    text: 'Sélectionnez  un motif',
                    value: null
                }, 'Informations', 'Catalogue', 'Détails produits', 'Service après vente']
            }
        },
        computed: {
            emailValidation() {
                return /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.form.email);
            }
        },
        methods: {
            getShops() {
                this.$prismic.client.query([this.$prismic.Predicates.at('document.type', 'shop')])
                    .then((response) => {
                        for (let res of response.results) {
                            this.shops.push(res.data);
                        }
                    })
            },
            onSubmit(evt) {
                evt.preventDefault();
                console.log('Send mail');
                /*alert(JSON.stringify(this.form));*/
            }
        },
        created() {
            this.getShops();
        }
    }
</script>
